.App {
  text-align: center;
  color: #ff0000;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}
.App-header {
  color: #ffffff;
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.text-box {
  text-align: center;
  color: #ffffff;
}

.header {
  color: #ffffff;
  -webkit-animation-name: headerani;
          animation-name: headerani;
  -webkit-animation-duration: 10s;
          animation-duration: 10s;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
}

@-webkit-keyframes headerani {
  0% {
    color: rgb(255, 72, 72);
  }
  15% {
    color: rgb(255, 246, 115);
  }
  30% {
    color: rgb(123, 255, 145);
  }
  45% {
    color: rgb(124, 255, 255);
  }
  60% {
    color: rgb(132, 123, 255);
  }
  75% {
    color: rgb(255, 115, 248);
  }
  90% {
    color: rgb(255, 72, 72);
  }
}

@keyframes headerani {
  0% {
    color: rgb(255, 72, 72);
  }
  15% {
    color: rgb(255, 246, 115);
  }
  30% {
    color: rgb(123, 255, 145);
  }
  45% {
    color: rgb(124, 255, 255);
  }
  60% {
    color: rgb(132, 123, 255);
  }
  75% {
    color: rgb(255, 115, 248);
  }
  90% {
    color: rgb(255, 72, 72);
  }
}
.navigationBtn {
  background-color: #50A4B9;
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  overflow: hidden;
  background: radial-gradient(ellipse at bottom, #0c455f 0%, #00222c 100%);
  background-size: 400%, 400%;
  -webkit-animation: gradient 10s ease infinite;
          animation: gradient 10s ease infinite;
}

#root {
  width: 100vw;
  height: 100vh;
  align-items: center;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

.main {
  width: 100%;
  height: 100%;
}

.navigation {
  height: 100px;
  width: -webkit-max-content;
  width: max-content;
  margin-bottom: calc(100% - 100px);
}

@-webkit-keyframes gradient {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}

@keyframes gradient {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}
.App {
  background-color: rgba(0, 0, 0, 0.1);
  margin-left: 15vw;
  margin-right: 15vw;
  height: 100vh;
}
